<template>
  <div>
    <li class="item">
      <div class="item-text" :class="{completed : todo.completed}" v-if="todo.editing== false">{{ todo.title }} </div>
      <input type="text" class="item-input" v-model="editTitle" @keyup.enter="updateTodo(todo)" @keyup.esc="cancelTodo(todo)" v-else> 
      <div class="item-icons">
        <i class="item-icon fas fa-pencil-alt" @click.prevent="editTodo(todo)"></i>
        <i class="item-icon fas fa-trash-alt" @click.prevent="removeTodo(todo.id)"></i>
        <input type="checkbox" v-model="todo.completed" @change="updateTodo(todo)">   
      </div> 
    </li>
  </div>
</template>
<script>
export default {
  data() {
    return {
      editTitle: '',
    }
  },
  props: ['todo'],
  computed: {

  },
  methods: {
    removeTodo(id) {
      //console.log(id)
      this.$store.dispatch('removeTodo', id)
    },
    editTodo(todo) {
      //console.log(todo)
      todo.editing = true
    },
    cancelTodo(todo) {
      todo.editing = false
    },
    updateTodo(todo) {
      todo.editing = false
      todo.title= this.editTitle || todo.title
      this.$store.dispatch('updateTodo', todo)
      //console.log(todo)
    },
  },
  created() {

  }
}
</script>