<template>
  <div class="container">
    <h1>My Todo List</h1>
    <div class="add">
      <input type="text" class="add-input" v-model="newTitle" @keyup.enter="addTodo" placeholder="Add New Todo">
    </div>
    <div class="output">
      <h3>Todo :</h3>
      <ul class="list">
        <Todo v-for="todo in todos" :key="todo.id" :todo="todo"></Todo>
      </ul>
    </div>
    <div class="output">
      <h3>Done :</h3>
      <ul class="list">
        <Todo v-for="todo in dones" :key="todo.id" :todo="todo"></Todo>
      </ul>
    </div>
  </div>
</template>
<script>
import {mapGetters, mapActions} from 'vuex'
import Todo from '../components/Todo.vue'

export default {
  data() {
    return {
      newTitle: '',
      editTitle: ''
    }
  },
  components: {
    Todo
  },
  computed: {
    ...mapGetters(['todoList', 'todos', 'dones'])
  },
  methods: {
    ...mapActions(['getTodoList']),
    addTodo() {
      this.$store.dispatch('addTodo', {
        title: this.newTitle,
      })
      this.newTitle = ''
    }
  },
  created() {
    this.getTodoList()
  }
}
</script>